
@import url(./Home_m.css);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 27px;
  min-width: 1024px;
}
.loaded-1 {
  width: 140px;
  height: 270px;
  background-repeat: no-repeat;
  background-image: url('http://tsuredure-project.jp/img/logo_t.png');
  background-size: 140px 270px;

  /* -webkit-animation: fade 1s 2.2s forwards;
  -moz-animation: fade 1s 2.2s forwards;
  -o-animation: fade 1s 2.2s forwards;
  animation: fade 1s 2.2s forwards; */
}
@-webkit-keyframes fade {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fade {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.loaded-1 ._1,
.loaded-1 ._2,
.loaded-1 ._3 {
  position: absolute;
  top: 0;
  width: 1px;
  background-color: #a5a6a6;
  display: block;
  /* height: 100%; */
  /* opacity: 1 !important; */
}
.loaded-1 ._1 {
  -webkit-animation: imganim 1.5s 1.6s cubic-bezier(0.165, 0.84, 0.44, 1);
  animation: imganim 1.5s 1.6s cubic-bezier(0.165, 0.84, 0.44, 1);

  -webkit-webkit-animation-fill-mode: forwards;
  -moz-webkit-animation-fill-mode: forwards;
  -ms-webkit-animation-fill-mode: forwards;
  -o-webkit-animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.loaded-1 ._2 {
  -webkit-animation: imganim 1.5s 1.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  animation: imganim 1.5s 1.8s cubic-bezier(0.165, 0.84, 0.44, 1);

  -webkit-webkit-animation-fill-mode: forwards;
  -moz-webkit-animation-fill-mode: forwards;
  -ms-webkit-animation-fill-mode: forwards;
  -o-webkit-animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.loaded-1 ._3 {
  -webkit-animation: imganim 1.5s 2s cubic-bezier(0.165, 0.84, 0.44, 1);
  animation: imganim 1.5s 2s cubic-bezier(0.165, 0.84, 0.44, 1);

  -webkit-webkit-animation-fill-mode: forwards;
  -moz-webkit-animation-fill-mode: forwards;
  -ms-webkit-animation-fill-mode: forwards;
  -o-webkit-animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes imganim {
from {
    height: 0;
}
to {
    height: 100%;
}
}
@keyframes imganim {
from {
    height: 0;
}
to {
    height: 100%;
}
}
.loaded li {
  text-transform: uppercase;
  font-size: 17px;
  text-align: left !important;
  margin-top: 17px;
  line-height: 17px;
}
.loaded li a {
  position: relative;
  color: #a5a6a6;
  transition: all .3s ease-out;
}
.loaded li a:hover {
  color: #1a1a1a !important;
}
.loaded li a.grow::after {
  content: "";
  height: 1px;
  width: 0;
  background-color: #3fcaab;
  position: absolute;
  bottom: -5px;
  left: 0;
  transition: width 500ms ease;
}
.loaded li a.grow:hover::after {
  width: 100%;
}
.sns {
  float: left;
  margin-top: 17px;
}
.sns li {
  margin-right: 17px;
  display: inline-block;
}
.sub_content li {
  float: left;
  width: 100%;
}
@-webkit-keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.fade-in {
  opacity: 0;
  -webkit-animation: fadeIn ease-in 1;
  animation: fadeIn ease-in 1;

  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;

  -webkit-animation-duration: 1s;
  animation-duration: 1s;

  -webkit-animation-delay: 1.2s;
  animation-delay: 1.2s;
}
.sub_content li a {
  display: block;
}
.sub_content li a img {
  width: 180px;
  height: auto;
}
.content {
  color: #ffffff;
  background-color: #3fcaab;
  font-size: 14px;
  text-align: left;
  width: 100%;
  height: 148px;
  padding: 34px 0 0 0;
  margin: 0 0 0 0 !important;
}
.content h2 {
  font-size: 14px !important;
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  color: #ffffff !important;
}
.content .top_news {
  height: 52px;
}
.content .top_news .time {
  font-size: 10px;
  position: absolute;
  top: 0;
  float: right;
  margin-left: 67px;
}
.content a {
  font-size: 10px !important;
  text-decoration: none !important;
  color: #ffffff !important;
  position: absolute !important;
  bottom: 0 !important;
  float: right !important;
  margin-left: 67px;
}
.more_buttom {
  position: absolute;
  border: 1px solid white;
  top: -25px;
  right: 20px;
}
.more_buttom a {
  position: relative !important;
  text-align: center;
  display: block;
  font-size: 10px;
  text-decoration: none;
  color: #ffffff;
  margin: 1px 10px 1px 10px !important;
}
.content .top_twitter {
  height: 80px;
  margin-left: 5%;
}
.top_twitter .tweet_wrap {
  position: absolute;
  top: 0;
  left: 0;
  height: 80px;
}
.top_twitter p {
  display: block;
}
.top_twitter a {
  position: relative !important;
}
.lb {
  position: fixed;
  z-index: 190;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: auto;
  height: auto;
}
.lb-close {
  display: block;
  position: absolute;
  right: 1em;
  top: 1em;
  width: auto;
  height: auto;
  text-align: center;
  line-height: 0;
  padding: .5rem .5rem;
}
.lb-overlay {
  background-color: #3fcaab;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 200;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.close {
  position: relative;
  display: inline-block;
  width: 35px;
  height: 35px;
  overflow: hidden;
}
.close::before,
.close::after {
  content: '';
  position: absolute;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #fff;
}
.close::before {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.close::after {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.close.hairline::before,
.close.hairline::after {
  height: 2px;
}
.batch {
  background-color: #b0e45d;
  height: 45px;
  width: 100%;
}
.sp_text {
  padding: 5px 0 5px 0;
  color: white;
  line-height: 35px;
  display: block;
  font-size: 18px;
}



