
@import url(./components/Home_m.css);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.animation-blur-load {
  -webkit-animation-name: blur-load;
          animation-name: blur-load;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
@-webkit-keyframes blur-load {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}
}
@keyframes blur-load {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}
}
footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 33px;
  background-color: #26bc9b;
}
footer .right {
  font-size: 10px;
  color: white;
  line-height: 33px;
  float: left;
  margin-left: 220px;
}
footer .banner {
  padding-top: 7px;
  float: right;
  height: 19px;
  margin-right: 100px;
}
footer li {
  display: inline;
}



